<script>
let api = new URL("http://45.162.168.170:8081");
api.pathname = "ventas";

//api.port = 8000;
//api.port = 8081;

import VueHtml2pdf from "vue-html2pdf";
import { mapState, mapActions } from "vuex";
import axios from "axios";
import "jspdf/dist/polyfills.es.js";
import { jsPDF } from "jspdf";
