            tabla_clientes: [],
        },
      productos: [],
      datachart: {
            labels: [
            ],
            datasets: []
        },
      fields: [
          { key: "id_venta", label: "ID", sortable: true },
          { key: "tipo_venta", label: "Tipo", sortable: true },
          { key: "fecharealizacion", label: "Fecha", sortable: true },
          { key: "gananciatotal", label: "Total", sortable: true },
          { key: "id_reparto", label: "Reparto", sortable: true },
          { key: "id_stock", label: "Stock", sortable: true },
          { key: "id_cliente", label: "Cliente", sortable: true },
          { key: "totalpagado", label: "Total Pagado", sortable: true },
          { key: "totalpagadomp", label: "Total Pagado con MP", sortable: true },
          { key: "diferencia", label: "Diferencia", sortable: true },
          { key: "action", label: "", variant: "secondary" },
          //{ key: 'yavisitado', label: 'Ya Visitado', sortable: true, },
          //{ key: "action", label: "Acciones", variant: "secondary" },
      ],
      totalRows: 1, //Total de filas
      currentPage: 1, //Pagina actual
      perPage: 10, // Datos en la tabla por pagina
      buscar: "",
      op_repartos: [],
      fechausada: null,
      sortableOptions: {
        chosenClass: 'is-selected'
      },
      token: "",
        infoVisitar: {
        id: "modal_visitar",
        cliente: -1,
      },
      ordenAPDF: {}, //Se carga cuando se hace clic en exportar a pdf, con la orden a exportar
    };
  },
  computed: {
    rows() {
      return this.tabla_ventas.length;
    },
    id() {
        return this.tabla_ventas.numero_orden;
    },
    items() {
      return tabla_ordenes.filter((item) => {
        return item.numero_orden
          .toLowerCase()
          .includes(this.buscar.toLowerCase());
      });
    },
  },
  methods: {
    fechausadacambio()
    {
        this.setGraph();
        this.testFetch();
    },
    cambiarrepausado(id)
    {
        this.repartousado.repa = this.getReparto(id);
        this.setGraph();
        this.testFetch();
    },
    async testFetch() {
        try {
            const res = await fetch(api);
            const data = await res.json();
            let totalventas = data.results.length;
            this.datachart.datasets = [];
            this.datachart.labels = [];
            let datasetear = {
                label: 'Ventas por Reparto',
                data: [],
                backgroundColor: [
                    'rgb(276, 99, 132)',
                    'rgb(54, 162, 235)',
                    'rgb(255, 205, 86)',
                    'rgb(0, 0, 0)',
                    'rgb(255, 165, 0)',
                    'rgb(137, 30, 53)'
                ],
                hoverOffset: 4
            };
            let opcionrepacopia = {
                repa: null,
                lista_id: [],
                tabla_clientes: [],
            };
            opcionrepacopia.repa = this.repartousado.repa;
            opcionrepacopia.repa.clientes_reparto.forEach(elemento => opcionrepacopia.lista_id.push(parseInt(elemento.split("/")[4])));
            this.listacli.forEach((clien) => {
                let opcionclien = {
                    cliente: clien,
                    tabla_ventas: [],
                };
                this.datachart.labels.push('Cliente ' + clien.numero_cliente);
                for (var i = 0; i < data.results.length; i++) {
                    if ((data.results)[i].fecharealizacion == this.fechausada) {
                        if (((data.results)[i]).id_reparto.split('/')[4] == this.repartousado.repa.id_reparto) {
                            if (((data.results)[i]).id_cliente.split('/')[4] == clien.numero_cliente) {
                                opcionclien.tabla_ventas.push((data.results)[i])
                            }
                        }
                    }
                }
                console.log(opcionclien);
                if (opcionrepacopia.lista_id.includes(clien.numero_cliente)) {
                    opcionrepacopia.tabla_clientes.push(opcionclien)
                }
                datasetear.data.push(opcionclien.tabla_ventas.length);
                this.datachart.datasets.push(datasetear);
                this.repartousado = opcionrepacopia;
                console.log('opcion completa');
                console.log(opcionrepacopia);
            });
            console.log(this.repartoslista);
            this.setGraph();
        } catch (error) {
            console.log(error);
        }
    },
    async getRepartos() {
        let productosAPI = new APIControler();
        productosAPI.apiUrl.pathname = "repartos/";
        this.datarep = await productosAPI.getData();
        let option
        this.datarep.forEach((element) => {
            option = {};
            option.value =
                element.id_reparto;
            option.text = element.nombre;
            console.log(option);
            this.op_repartos.push(option);
        });
    },
    async getProductos() {
        let serviciosAPI = new APIControler();
        serviciosAPI.apiUrl.pathname = "productos/";
        this.productos = await serviciosAPI.getData()
    },
    async getClientes() {
        let clientesAPI = new APIControler();
        clientesAPI.apiUrl.pathname = "clientes/";
        this.listacli = await clientesAPI.getData()
    },
    setGraph()
    {
        let ctx = document.getElementById('chart');
        if (this.chart) {
            this.chart.destroy();
        }
        this.chart = new Chart(ctx, {
            type: 'bar',
            data: this.datachart,
            options: {
                title: {
                    text: "Cantidad de ventas por Reparto",
                    display: true
                }
            }
        });
    },
    async testFetch2() {
      try {
        if(typeof this.$route.params.repa ==="undefined" ){
          console.log("Reparto vacio")
          const baseURL="http://45.162.168.170:8081";
          const response = await axios.get(baseURL+"/repartos/"+this.$route.query.id)
          this.reparto=response.data
        }

        const res = await fetch(api);
        const data = await res.json();

        var lista_clientes = data.results.filter(item=>{
          return item.id_medico.split('/')[4]==this.profesional.id_medico
          });

        this.tabla_ordenes = lista_ordenes;
      } catch (error) {
        console.log(error);
      }
    },

    //Funcion para mostrar el modal
    showModal() {
      this.$refs["my-modal"].show();
    },
    showModalinfo(item, index) {
      this.infoVisitar.cliente = item;
      this.showModal();
    },
    //Funcion para esconder el modal
    hideModal() {
      this.$refs["my-modal"].hide();
    },
    fechaactual() {
        const f = new Date();
        console.log(f.toLocaleDateString());
        const formatDate = (d) => {
            return ('0' + d.getDate()).slice(-2) + "-" + ('0' + (d.getMonth() + 1)).slice(-2) + "-" + d.getFullYear()
        }
        console.log(formatDate(f));
        return (formatDate(f));
    },
    fechaactual2() {
            const f = new Date();
            const formatDate = (d) => {
                return d.getFullYear() + "-" + ('0' + (d.getMonth() + 1)).slice(-2) + "-" + ('0' + d.getDate()).slice(-2)
            }
            console.log(formatDate(f));
            return (formatDate(f));
    },
    async buscarnow() {
      // Declare variables
      var input,
        filter,
        table,
        tr,
        td,
        i,
        txtValue,
        p1, //nro de orden
        p2, //nro de socio
        p3, //servicio
        p4, //id medico
        p5; //fecha
      input = this.$refs.buscadorlista;
      filter = input.value.toUpperCase();
      table = document.getElementById("tablaregistros");
      tr = table.getElementsByTagName("tr");

      // Loop through all list items, and hide those who don't match the search query
      for (i = 1; i < tr.length; i++) {
        td = tr[i].getElementsByTagName("td");
        p1 = td[0].textContent || td[0].innerText;
        p2 = td[1].textContent || td[1].innerText;
        p3 = td[3].textContent || td[3].innerText;
        p4 = td[4].textContent || td[4].innerText;
        p5 = td[6].textContent || td[6].innerText;
        txtValue = p1 + p2 + p3 + p4 + p5;
        if (txtValue.toUpperCase().indexOf(filter) > -1) {
          tr[i].style.display = "";
        } else {
          tr[i].style.display = "none";
        }
      }
    },
    getReparto(id)
    {
        let reparto = this.datarep.find(s => {
            return s.id_reparto == id
        })
        return reparto;			
	},
    inicializar()
    {
        if (this.$session.get('tipocuenta') == 'reparto')
        {
            console.log(this.$session.get('repartused'));
            this.reparto = this.$session.get('repartused');
            
        }
        this.fechausada = this.fechaactual2();
    },
    async generarPDF() {
        const $elementoParaConvertir = document.getElementById('divpdf'); // <-- Aqu� puedes elegir cualquier elemento del DOM
        html2pdf()
            .set({
                margin: 1,
                filename: 'documento.pdf',
                image: {
                    type: 'jpeg',
                    quality: 0.98
                },
                html2canvas: {
                    scale: 3, // A mayor escala, mejores gr�ficos, pero m�s peso
                    letterRendering: true,
                },
                jsPDF: {
                    unit: "in",
                    format: "a4",
                    orientation: 'portrait' // landscape o portrait
                }
            })
            .from($elementoParaConvertir)
            .save()
            .catch(err => console.log(err));
    },
  },
  beforeMount() {
    this.getRepartos();
    this.inicializar();
    this.getClientes();
    this.getProductos();
  },
};
</script>

